@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css);
*:focus {
  outline: none;
}

.genre-container {
  padding: 25px 0;
}

.genre-buttons {
  font-size: 20px;
  width: 200px;
  height: 100px;
  border-radius: 20px;
}

audio {
  margin: 40px 0;
}

.guessDisplay {
  margin: 10px 0;
}

.giveup-button {
  padding: 0px 10px;
  border-radius: 10px;
}

img {
  display: block;
  margin: 10px auto;
}

.gamestats-container {
  margin: 0 auto 40px auto;
  text-align: center;
  width: 300px;
}

.gamestats-container p {
  display: flex;
  margin: 0;
  padding: 2px 0;
}

.gamestats-container span {
  width: 300px;
  text-align: right;
}
.gamestats-container span:last-of-type {
  text-align: left;
  margin-left: 5px;
}

a {
  color: #e0e0e0; /* gray */
}
a:hover {
  color: white;
  text-decoration: none;
}

input {
  /* for input text field */
  padding: 0 5px;
}

input:focus {
  outline: 0;
}

button {
  /* all buttons */
  border: none;
  background-color: white;
  margin: 3px;
  border: 1px solid white;
}

button:focus {
  outline: 0;
}

button:hover {
  border: 1px solid gray;
}

.App {
  min-height: 100vh;
  width: 100%;
  text-align: center;
  padding: 30px 20px;
  font-family: "Segoe UI", "Tahoma", "Geneva", "Verdana", sans-serif;
  margin: 0;
  color: white;
}

.container {
  min-height: 80vh;
}

footer {
  bottom: 0;
  width: 100%;
  padding: 30px 0;
}

